import http from './http'
// var url = 'http://192.168.0.91:8087'

/**
 * 
 * THRIDCODE_0001  
 * THRIDCODE_0007  
 */
export const getLogin = (corpId, code) => http.get("/caa01/authCodeLogin", { params: { corpId: corpId, code: code } })

/**
 * token
 */
export const getToken = () => http.get('/caa01/getAccessTokenByStoreId', { storeId: 1 })

/**
 *  JSAPI 
 * agentIdcorpIdtimeStampnonceStrsignature
 */
export const getJSApi = (corpId, httpUrl) => http.get('/caa01/getConfig', { params: { corpId: corpId, httpUrl: httpUrl } })

/**
 * ticket
 */
export const ticket = (access_token) => http.get('/get_jsapi_ticket', { access_token: access_token })

/**
 * 
 */
export const getUserId = (corpId, code, phone, userPwd) => http.post("/caa01/bindUserInfo", { corpId: corpId, code: code, phone: phone, userPwd: userPwd })

/**
 * 
 */
export const updateRedis = (nonce, timestamp, signature, param) => http.post("/updateRedis", { nonce: nonce, timestamp: timestamp, signature: signature, param: param })


