 <!--  -->
<template>
  <van-tabbar v-model="active" inactive-color="#777777" active-color="#000000">
    <van-tabbar-item v-for="(item,index) in tabbars" :key="index" :to="(item.name)">
      <span>{{item.title}}</span>
      <img slot="icon" slot-scope="props" :src="props.active ? item.active : item.normal" />
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
export default {
  data() {
    return {
      active: 0
    }
  },
  mounted() {
    if (this.$route.name == "mechanicIndex" || this.$route.name == "bossIndex") {
      this.active = 0;
    } else if (this.$route.name == "sell/index" || this.$route.name == "mechanicMine") {
      this.active = 1;
    } else if(this.$route.name == 'house/index') {
      this.active = 2;
    } else if(this.$route.name == 'mechanicState') {
      this.active = 3;
    } else if(this.$route.name == 'bossMine') {
      this.active = 4;
    }
  },
  props:['tabbars']
}
</script>

