/**
 * 
 */
export function setStore(name, value) {
    if (!name) {
        throw new Error('storename')
    }
    if (!value) {
        throw new Error(`${name}value`)
    }
    let valueString = value;
    if (typeof value !== 'string') {
        valueString = JSON.stringify(value);
    }
    window.localStorage.setItem(name, valueString);
}

/**
 * 
 */
export function getStore(name) {
    if (!name) return;
    const value = window.localStorage.getItem(name);
    try {
        return JSON.parse(value);
    } catch (e) {
        return value;
    }
}

/**
 * 
 */
export function removeStore(name) {
    if (!name) return;
    window.localStorage.removeItem(name);
}

/**
 * 
 */
export const getTime = (value) => {
    let date = new Date(value);
    let y = date.getFullYear();
    let MM = date.getMonth() + 1;
    MM = MM < 10 ? "0" + MM : MM;
    let d = date.getDate();
    d = d < 10 ? "0" + d : d;
    let h = date.getHours();
    h = h < 10 ? "0" + h : h;
    let m = date.getMinutes();
    m = m < 10 ? "0" + m : m;
    let s = date.getSeconds();
    s = s < 10 ? "0" + s : s;
    return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
}


/**
 * 
 * setMoney  ,  
 * formatDecimal 
 */
export const setMoney = (value) => {
    value = parseFloat((value + "").replace(/[^\d\.-]/g, "")).toFixed(2) + ""
    var valueArr = value.split(".")[0].split("");
    const valueFloat = value.split(".")[1];
    let valueString = "";
    for (let i = 0; i < valueArr.length; i++) {
        valueString +=
            valueArr[i] +
            ((i + 1) % 3 == 0 && i + 1 != valueArr.length ? "," : "")
    }
    const money = valueString.split("").join("") + "." + valueFloat
    return money
}

export const formatDecimal = (num, decimal) => {
    num = num.toString();
    var index = num.indexOf('.');
    if (index !== -1) {
        num = num.substring(0, decimal + index + 1)
    } else {
        num = num.substring(0)
    }
    return parseFloat(num).toFixed(decimal)
}

/**
 * url
 */
export const getUrlParam = (name) => {
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
}

/**
 * 
 */
export const randomString = (e) => {
    e = e || 32;
    var t = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678",
        a = t.length,
        n = "";
    for (var i = 0; i < e; i++) n += t.charAt(Math.floor(Math.random() * a));
    return n
}

/**
 *  
 */
export const getDifference = (startTime, endTime) => {
    var date1 = startTime; //
    var date2 = endTime; //
    console.log(date2)
    var date3 = new Date(date2).getTime() - new Date(date1).getTime(); //
    //
    var days = Math.floor(date3 / (24 * 3600 * 1000));
    //
    var leave1 = date3 % (24 * 3600 * 1000); //
    var hours = Math.floor(leave1 / (3600 * 1000));
    //
    var leave2 = leave1 % (3600 * 1000); //
    var minutes = Math.floor(leave2 / (60 * 1000));
    //
    var leave3 = leave2 % (60 * 1000); //
    var seconds = Math.round(leave3 / 1000);
    // console.log(
    // "  " +
    // days +
    // " " +
    // hours +
    // " " +
    // minutes +
    // " " +
    // seconds +
    // " "
    // );
    var difference = days + " " + hours + " " + minutes + " " + seconds + " ";
    return difference
}

/** */
export const getWeek = () => {
    var new_Date = new Date()
    var timesStamp = new_Date.getTime();
    var currenDay = new_Date.getDay();
    var dates = [];
    for(var i = 0; i < 7; i++) {
        dates.push(new Date(timesStamp + 24 * 60 * 60 * 1000 * (i - (currenDay + 7) % 7)).toLocaleDateString().replace(/[]/g, '-').replace(/[]/g, ''));
    }
    return dates
}