// vant
import { Toast } from 'vant';

/**
 *  
 */
export function openLoading(){
  Toast.loading({
    message: '...',
    forbidClick: true,
    loadingType: 'spinner',
    duration:0
  });
}

/**
 *  
 */
export function closeLoading(){
  Toast.clear()
}


/**
 * toast 
 */
export function openToast(message,timeOut){
  Toast({message,duration:timeOut})
}