import axios from "axios";
import qs from 'qs'
import { getStore } from '../util/store';
import { openLoading, closeLoading,openToast } from '../util/vant'

// hook
axios.interceptors.request.use(
  function (config) {
    openLoading();
    console.log(config)
    // token
    var paramName = config.url;
    var authCodeLogin = paramName.split('/')[2];
    var bindUserInfo = paramName.split('/')[2];
    var getAccessTokenByStoreId = paramName.split('/')[2];
    var dd = paramName.split('/')[2]
    if (authCodeLogin !== 'authCodeLogin' && bindUserInfo !== 'bindUserInfo' && getAccessTokenByStoreId !== 'getAccessTokenByStoreId' && dd !== 'oapi.dingtalk.com') {
      // config.headers['Authorization'] = getStore('token');
    }
    //post 
    config.headers['Content-Type'] = 'application/x-www-form-urlencoded';
    if (config.method == 'post') {
      config.data = qs.stringify({
        ...config.data //axiospostjson,qs
      })
    }
    return config;
  },
  function (error) {
    //  HTTP 
    // TODO
    return Promise.reject(error);
  }
);

// hook
axios.interceptors.response.use(
  //  ---> 
  function (response) {
    closeLoading();
    const { data } = response;
    if (data.code === '200') {
      return data;
    } else {
      openToast(data.msg || data.message)
    }
  },
  //  ---> 
  function (error) {
    closeLoading();
    openToast(error)
    return Promise.reject(error);
  }
)

export default axios;
