export const percetRing = {
  legend: {
    icon: "circle", //  
    itemWidth: 10, // 
    itemHeight: 10,
    formatter: function(name) {
      var index = 0;
      var percent = 0;
      var total = 0;
      data.columns.forEach(function(value, i) {
        if (value == name) {
          index = i;
        }
      });
      data.rows.forEach(function(value, i) {
        total += parseInt(value.money);
      });

      if (total === 0) {
        return name + "  " + 0 + "%";
      } else {
        percent = ((data.rows[index].money / total) * 100).toFixed(2);
        return name + "  " + percent + "%";
      }
    }
  }
};