/**
 * code
 */
export function powerCode() {
  return new Promise((resolve, reject) => {
    // var corpId = getStore('corpId')
    var corpId = 'ding4a3ef860bd74563935c2f4657eb6378f'
    dd.ready(function () {
      dd.runtime.permission.requestAuthCode({
        corpId: corpId,
        onSuccess: function (result) {
          resolve([result, corpId]);
        },
        onFail: function (err) {
          console.log(err)
          reject(err);
        }
      });
    });
  });
}

/**
 * 
 */
export function headTop(name, rightBtn) {
  return new Promise((resolve, reject) => {
    dd.biz.navigation.setTitle({
      title: name, //
      onSuccess: function (result) {
        if (rightBtn != undefined && rightBtn != '') {
          var rightBtns = rightBtn
          var show = true
        } else {
          var rightBtns = ''
          var show = false
        }
        dd.biz.navigation.setRight({
          show: show, // true  false  true
          control: true, //true false  false
          text: rightBtns, //
          onSuccess: function (result) {
            resolve(result);
          },
          onFail: function (err) { }
        });
      },
      onFail: function (err) { }
    });
  })
}

/**
 * 
 * typeqrCode()barCode()all()allqrCodebarCodetypeall
 */
export function sweepCode(typeName) {
  return new Promise((resolve, reject) => {
    if (typeName == '' && typeName == undefined) {
      typeName = all
    }
    dd.biz.util.scan({
      type: typeName,
      onSuccess: function (data) {
        resolve(data)
      },
      onFail: function (err) {
        reject(err)
      }
    });
  })
}

/**
 * 
 */
export function openDataTime() {
  return new Promise((resolve, reject) => {
    var currentTime = getTime(new Date().getTime()).substring(0, 10);
    dd.biz.util.datepicker({
      format: "yyyy-MM-dd", //formatandroid2015-03-31yyyy-MM-dd
      value: currentTime, //
      onSuccess: function (result) {
        resolve(result)
      },
      onFail: function (err) {
        reject(err)
      }
    });
  })
}

