/**
 * 
 */
export function getMap() {
  return new Promise((resolve, reject) => {
    dd.device.geolocation.get({
      targetAccuracy: Number,
      coordinate: Number,
      withReGeocode: Boolean,
      useCache: true, //truefalse
      onSuccess: function (result) {
        resolve(result)
      },
      onFail: function (err) {
        reject(err)
      }
    });
  })
}

/**
 * 
 */
export function openMap(latitude, longitude) {
  return new Promise((resolve, reject) => {
    dd.biz.map.locate({
      latitude: latitude, // 
      longitude: longitude, // 
      onSuccess: function (result) {
        resolve(result)
      },
      onFail: function (err) {
        this.common_vant.Toast(err)
      }
    });
  })
}

/**
 *  ()
 */
export function mapTitle(latitude, longitude, title) {
  return new Promise((resolve, reject) => {
    dd.biz.map.view({
      latitude: latitude, // 
      longitude: longitude, // 
      title: title // /POI
    });
  })
}

